@import "variables";

body {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
}

@import "header";
@import "nav";

.items__container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 0 40px;
}

.item__container {
  display: flex;
  justify-content: center;
  align-items: center;
  
  width: 200px;
  height: 403px;
  margin: 0 15px;
  margin-top: 50px;

  background: #fff;
  border: 1px solid #f3f3f3;
  box-sizing: border-box;
  border-radius: 5px;
}

.conteiner {
  width: 170px;
  height: 403px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.item__container__img {
  padding-top: 30px;
}

.item__img {
  width: 160px;
  transition: transform 1s;
}
$borderWidth: 3px;
$paddingY: 12px;
$marginY: $paddingY + $borderWidth;

.item__img:hover {
  border: $borderWidth solid #f3f3f3;
  padding: $paddingY 0;
  margin: (-$marginY) (-$borderWidth);
  transform: scale(1.2);
}

.item__info {
  margin-top: 45px;
  width: 170px;

  font-weight: 500;
  font-size: 12px;
  color: #000;
}

.item__code {
  width: 170px;
  margin-top: 6px;
  font-size: 11px;
}

.block_rat__rev {
  display: flex;
  justify-content: space-between;
  width: 170px;
}

.item__rating {
  display: flex;
  margin-top: 15px;
}

.item__stars {
  width: 15px;
  margin-right: 4px;
  background-position: 50% 50%;
  background-image: url(images/star.svg);
  background-repeat: no-repeat;
}

.item__rating > :nth-child(-1n+4) {
  background-image: url(images/star-active.svg);
  background-repeat: no-repeat;
}

.item__review--p {
  margin-top: 16px;
  font-weight: normal;
  font-size: 11px;
  color: #000;
}

.conteiner__price {
  display: flex;
  width: 170px;
  justify-content: space-between;
}

.item__price {
  margin-top: 25px;
  font-size: 11px;
  color: #000;
}

.price__number {
  margin-top: 21px;
  font-weight: bold;
  font-size: 16px;
  color: #000;
}

.item__input {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 170px;
  height: 40px;
  margin-top: 21px;
  margin-bottom: 15px;
  background: $button-color;
  border-radius: 5px;
}

.link {
  width: 100%;
  text-align: center;
  font-size: 14px;
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  line-height: 40px;
}

.link_hover:hover {
  background: #fff;
  color: $button-color;
  border-radius: 5px;
  border: 1px solid;
}

.container--style {
  font-family: Roboto, sans-serif;
}
